.resume-container {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    min-height: fit-content;
    margin: 120px 0;
    /* background-color: #918f8f; */
    /* background-image: url("../assets/blob-3.svg");
    background-size: cover;
    background-position: center 10%;
    background-repeat: no-repeat; */
}
  
.resume-card {
    display: flex;
    height: 340px;
    width: 1000px;
    /* margin: 80px 0; */
}
  
.resume-bullets {
    box-shadow: 15px 0 9px -15px #1f2235;
    height: 100%;
    width: 320px;
}
  
.resume-bullet-details {
    flex-grow: 1;
    width: 600px;
    overflow: hidden;
    padding: 0 0 0 80px;
}
  
.experience-description {
    margin: 10px 0 0 0;
    text-align: justify;
}
  
.resume-description-text {
    font-size: 16px;
}
  
.resume-details-carousal {
    transition: transform 1s ease-out;
}
  
.bullet-container {
    display: flex;
    align-items: center;
    height: 100%;
    width: 100%;
    position: relative;
}
  
.bullets {
    width: 86%;
    position: relative;
    z-index: 2;
}
  
.bullet {
    display: flex;
    align-items: center;
    cursor: pointer;
    background-color: black;
    height: 40px;
    margin: 15px 0;
    padding: 0 8px;
    border-radius: 20px;
    width: 30px;
    transition: width 0.6s ease;
}
  
.bullet-label {
    font-size: 18px;
    white-space: nowrap;
    font-family: 'Lancelot', serif;
}
  
.bullet-logo {
    height: 16px;
    margin: 0 30px 0 0;
}
  
.bullet-icons {
    width: 34px;
    height: 100%;
    z-index: 1;
    background-color: black;
    position: absolute;
}
  
.selected-bullet {
    color: #c19400;
    width: 100%;
    font-weight: bold;
}
  
.resume-screen-container {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 360px;
    width: 100%;
}
  
.resume-heading {
    display: flex;
    flex-direction: column;
}
  
.resume-main-heading {
    position: relative;
    display: flex;
    justify-content: space-between;
}
  
.resume-main-heading span {
    color: black;
    font-size: 20px;
    font-weight: 600;
    font-family: "Poppins SemiBold";
}
  
.experience-description {
    max-width: 100%;
}
  
.heading-bullet {
    position: absolute;
    left: -30px;
    height: 15px;
    width: 15px;
    top: 5px;
    border-radius: 50%;
    background-color: black;
}
  
.resume-sub-heading {
    font-size: 18px;
    font-family: "Poppins SemiBold";
    color: var(--black);
    margin: 10px 0 0 0;
}
  
.resume-heading-description {
    font-size: 14px;
    text-align: justify;
}
  
.heading-date {
    background-color: black;
    padding: 4px 12px;
    font-size: 20px;
    border-radius: 14px;
    color: #c19400;
}
  
.skill-parent {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 50%;
}
  
.programming-skills-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}
  
.skill-percentage {
    position: relative;
    height: 15px;
    width: 70%;
    background-color: #c19400;
}
  
.skill-percentage .active-percentage-bar {
    height: 15px;
    transition: 0.8s ease;
    position: absolute;
    background-color: #1f2235;
}
  
.skill-parent span {
    font-family: "Poppins Bold";
    font-size: 16px;
    color: #85cde3;
}
  
  /* IPAD PRO RESPONSIVENESS */
@media only screen and (max-width: 1110px) {
    .resume-card {
      width: 880px;
    }
  
    .resume-bullet-details {
      padding: 0 0 0 70px;
    }
}
  
  /* IPAD RESPONSIVENESS */
@media only screen and (max-width: 1023px) {
    .resume-content {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  
    .bullets {
      width: 90%;
    }
  
    .resume-card {
      height: fit-content;
      display: flex;
      flex-direction: column;
      margin: 30px 0;
      align-items: center;
      width: 90%;
    }
  
    .resume-bullet-details {
      height: 360px;
      padding: 0;
      width: fit-content;
    }
  
    .resume-bullets {
      margin: 30px 0;
      width: 100%;
    }
  
    .selected-bullet {
      width: 100%;
    }
}
  